@import url(https://fonts.googleapis.com/css2?family=Merriweather:ital,wght@1,300&display=swap);
body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.login2 {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  color: white;
  margin-bottom: 10%;
}

.healtharticles {
  position: absolute;
  right: 2%;
  font-size: small;
}

.snippet {
  position: absolute;
  left: 2%;
  width: 2px;
  font-size: smaller;
  margin-top: 60px;
  font-size: large;
}
.logo3 {
  background-color: white;
}

.title {
  position: absolute;
  top: 0.5em;
  left: 0;
  width: 100%;
  margin: 0;
  text-align: center;
  font-family: "Crimson Text", serif;
}

.slogan {
  margin-top: 50%;
}

.navynav {
  background-color: blue;
  box-shadow: none;
}

.wrapper {
  padding-top: 50px;
  height: 100%;
  display: flex;
  flex-flow: row wrap;
  padding: 20px;
  justify-content: space-around;
  align-content: center;
  overflow: auto;
}

.ui {
    justify-content: center;
}

.social-buttons {
    margin: 1rem;
}



